.contactWrap {
  .emailWrap {
    width: 96%;
    max-width: 700px;
    margin: auto;
    padding: 20px;
    border-radius: 10px;
    background:#e7e7e7;
    box-sizing: border-box;

    p {
      text-align: center;
      font-size: 22px;
      font-weight: 700;
      margin:20px 0;
    }
  }
  form {
    margin: auto;
    width: 100%;
    max-width: 500px;
    span {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom:7px;
    }
    label {
      color:#5f5f5f;
      font-size:16px;
      font-weight: 800;

    }
    input[type="text"],
    input[type="email"] {
      width: 400px;
      height:40px;
      border-radius: 7px;
      border: none;
      padding:7px;
      font-size: 15px;
      color:#222;
      font-weight: 500;
      font-family: "Open Sans", sans-serif;
    }
    textarea {
      width: 400px;
      height: 200px;
      border-radius: 7px;
      border: none;
      padding:7px;
      font-size: 15px;
      color:#222;
      resize: none;
      font-weight: 500;
      font-family: "Open Sans", sans-serif;
    }
    input[type="submit"] {
      display: block;
      border:none;
      background: none;
      margin: auto;
      font-size: 17px;
      font-weight: 700;
      padding: 10px 0 0 0;
      border-bottom:1px solid #222;
      cursor: pointer;
      &:hover {
        color: $pointClr;
        border-bottom: 1px solid $pointClr;
      }
    }
  }
}

/* Mobile */
@media only screen and (max-width: 800px) {
  .contactWrap {
    .emailWrap {
      padding: 3vw;
      border-radius: 4vw;
      p {
        font-size: 5vw;
        margin:5vw 0;
      }
    }
    form {
      span {
        display: block;
        margin-bottom:5vw;
      }
      label {
        display: block;
        font-size:4vw;
        margin-bottom:1vw;
  
      }
      input[type="text"],
      input[type="email"] {
        width: 100%;
        height:10vw;
        border-radius: 2vw;
        padding:2vw;
        font-size: 4vw;
      }
      textarea {
        width: 100%;
        height: 40vw;
        border-radius: 2vw;
        padding:2vw;
        font-size: 4vw;
      }
      input[type="submit"] {
        font-size: 5vw;
        padding: 4vw 0 0 0;
        margin-bottom: 5vw;
      }
    }
  }
}